// Interface
import { IStocks } from 'src/interface';

export const stocks = (state: IStocks[] = [], action: { type: string, updater: IStocks | any }) => {
    switch (action.type) {
        case 'ADD_STOCK':
            return state.concat(action.updater);
        case 'DELETE_STOCK':
            return state.filter((stock) => {
                return stock.id !== action.updater.id;
            });
        case 'UPDATE_STOCK':
            return state.map((stock) => {
                return stock.id === action.updater.id ? action.updater : stock; 
            });
        case 'REPLACE_ENTIRE_LIST':
            return action.updater;
        case 'TOGGLE_STOCK_TYPE':
            return state.map((stock) => {
                return stock.id === action.updater.id ? { ...stock, isEtf: !stock.isEtf } : stock;
            });
        default:
            return state;
    }
};


