import * as React from 'react';
import { Dispatch, bindActionCreators } from 'redux';
import { connect } from 'react-redux';

import { DragDropContext } from 'react-dnd';
import HTML5Backend from 'react-dnd-html5-backend';
import StocksDisplayRow from './stocksDisplayRow';
const update = require('immutability-helper');

// Actions
import { deleteStock, replaceEntireList, toggleStockType } from 'src/actions/stockInputActions';

const style = {
    boxShadow: '0 0 0.1cm rgba(0,0,0,0.5)'
};

interface IStocksDisplayContainerProps {
    stocks: any;
    replaceEntireList: (stocks: any) => Dispatch<object>;
    toggleStockType: (stocks: any) => Dispatch<object>;
    deleteStock: (stocks: any) => Dispatch<object>;
}
interface IStocksDisplayContainerState {

}

class StocksDisplayContainer extends React.Component<IStocksDisplayContainerProps, IStocksDisplayContainerState> {
    constructor(props: any) {
        super(props);
        this.state = {

        };
    }
    moveRow = (dragIndex: number, hoverIndex: number) => {
        const { stocks } = this.props;
        const dragRow = stocks[dragIndex];
        
        let updatedProps = update(this.props, {
            stocks: {
                $splice: [[dragIndex, 1], [hoverIndex, 0, dragRow]],
            },
        });
        this.props.replaceEntireList(
            updatedProps.stocks
        );
    }
    render() {
        // console.log(this.props.stocks);
        return (
            <div style={style}>
                {this.props.stocks.map((stock: any, index: number) => (
                // {this.state.rows.map((stock: any, index: number) => (
                    <StocksDisplayRow
                        key={stock.id}
                        index={index}
                        id={stock.id}
                        // text={row.text}
                        data={stock}
                        moveRow={this.moveRow}
                        toggleStockType={this.props.toggleStockType}
                        deleteStock={this.props.deleteStock}
                    />
                ))}
            </div>
        );
    }
}

function mapStateToProps(state: any) {
    return {
        stocks: state.stocks
    };
}

function mapDispatchToProps(dispatch: Dispatch<Object>) {
    return bindActionCreators(
        {
            deleteStock,
            replaceEntireList,
            toggleStockType
        },
        dispatch
    );
}

// export default connect(mapStateToProps, mapDispatchToProps)(StockInput);
export default connect(mapStateToProps, mapDispatchToProps)(DragDropContext(HTML5Backend)(StocksDisplayContainer));
// export default DragDropContext(HTML5Backend)(StockInput);


