import { Dispatch } from 'redux';
// Interface
import { IStocks } from 'src/interface';

export const addStock = (updater: IStocks | IStocks[]) => {
    return (dispatch: Dispatch<Object>) => {
        dispatch({
            type: 'ADD_STOCK',
            updater
        });
    };
};
export const deleteStock = (updater: IStocks) => {
    return (dispatch: Dispatch<Object>) => {
        dispatch({
            type: 'DELETE_STOCK',
            updater
        });
    };
};
export const replaceEntireList = (updater: IStocks[]) => {
    return (dispatch: Dispatch<Object>) => {
        dispatch({
            type: 'REPLACE_ENTIRE_LIST',
            updater
        });
    };
};
export const toggleStockType = (updater: IStocks) => {
    return (dispatch: Dispatch<Object>) => {
        dispatch({
            type: 'TOGGLE_STOCK_TYPE',
            updater
        });
    };
};


