import * as React from 'react';
import { Dispatch, bindActionCreators } from 'redux';
import { connect } from 'react-redux';

// Actions
import { addStock, replaceEntireList, toggleStockType } from 'src/actions/stockInputActions';
// Components (Common)
import TextInput from 'src/components/input';
// Interface
import { IStocks } from 'src/interface';

interface IStockInputProps {
    savedStocks: IStocks[];
    addStock: (updater: IStocks[]) => Dispatch<object>;
    replaceEntireList: (updater: IStocks[]) => Dispatch<object>;
    toggleStockType: (updater: IStocks) => Dispatch<object>;
}
interface IStockInputState {
    inputValue: string;
    inputError: boolean;
}

class StockInput extends React.Component<IStockInputProps, IStockInputState> {
    constructor(props: any) {
        super(props);
        this.state = {
            inputValue: '',
            inputError: false
        };
    }
    handleInputChange = (e: any) => {
        this.setState({ inputValue: e.currentTarget.value });
    }
    handleSubmit = (e: any) => {
        e.preventDefault();
        // Checks for existing stock IDs
        let existingStocksArray = this.props.savedStocks.map((stock) => {
            return stock.id;
        });
        let uniqueStocksInputChecker: string[] = [];
        let stocks = this.state.inputValue.split(',').map((id) => {
            if (uniqueStocksInputChecker.indexOf(id) === -1) {
                uniqueStocksInputChecker.push(id);
            } else {
                existingStocksArray = existingStocksArray.concat(id);
            } 
            return { id, isEtf: false };
        });
        let stockNumberCheck = stocks.filter((stock) => stock.id.length !== 5 || isNaN(Number(stock.id)) || existingStocksArray.indexOf(stock.id) > -1);
        if (stockNumberCheck.length > 0) {
            this.setState({ inputError: true });
            return;
        }
        this.setState({ 
            inputError: false,
            inputValue: ''
        });
        // 
        this.props.addStock(stocks);
        // localStorage.setItem('stocks', this.props.savedStocks.concat(stocks));
        // 
    }
    render() {
        return (
            // <form onSubmit={this.handleSubmit}>
            //     <input value={this.state.inputValue} onChange={this.handleInputChange} />
            //     <button>Submit</button>
            // </form>
            <div className="stock-input-container">
                <TextInput 
                    handleInputChange={this.handleInputChange}
                    inputValue={this.state.inputValue}
                    handleSubmitForm={this.handleSubmit} 
                    inputError={this.state.inputError}
                    buttonContent={'Submit'}
                />
            </div>
        );
    }
}

function mapStateToProps(state: any) {
    return {
        savedStocks: state.stocks
    };
}

function mapDispatchToProps(dispatch: Dispatch<Object>) {
    return bindActionCreators(
        {
            addStock,
            replaceEntireList,
            toggleStockType
        },
        dispatch
    );
}

export default connect(mapStateToProps, mapDispatchToProps)(StockInput);


