import { Dispatch } from 'redux';
// Interface
import { IStocks } from 'src/interface';

export const updateStock = (stocksList: IStocks[], index: number, apiKey: string, callback: any) => {
    return (dispatch: Dispatch<Object>) => {
        if (index < stocksList.length) {
            const quandlUrl = `https://www.quandl.com/api/v3/datasets/HKEX/${stocksList[index].id}.json?limit=30&api_key=fu5915sL3H6keg-FS5ys`;
            
            fetch(quandlUrl).then((response) => {
                return response.json();
            }).then((results) => {
                const { dataset } = results;
                const data = {
                    currentPrice: dataset.data[0][1],
                    currentBid: dataset.data[0][4],
                    high: dataset.data[0][7],
                    low: dataset.data[0][8],
                    prevCloseDay: dataset.data[0][9],
                    prevCloseMonth: dataset.data[29][9]
                    // changePercent: Math.floor(dataset.data)
                };
                // const changePercentDay = Math.floor(((data.currentPrice - data.prevCloseDay) / data.prevClose - 1) * 10000) / 100;
                // data['changePercent'] = changePercentDay;
                const updater = {
                    ...stocksList[index],
                    ...data,
                    name: dataset.name,
                };
                // console.log(updater);
                dispatch({
                    type: 'UPDATE_STOCK',
                    updater
                });
                callback(stocksList, index + 1, apiKey, callback);
            });

            // dispatch({
            //     type: 'UPDATE_STOCK',
            //     updater
            // });
            // callback(stocksList, index + 1, apiKey, callback);
        }
    };
};


