import * as React from 'react';
import { Dispatch, bindActionCreators } from 'redux';
import { connect } from 'react-redux';

// Dev
import store from 'src/reducers';
// Interface
import { IStocks } from 'src/interface';
// Actions
import { addApiKey } from 'src/actions/apiKey';
import { replaceEntireList } from 'src/actions/stockInputActions';
import { updateStock } from 'src/actions/stocksApiUpdate';
// Components (Common)
import Modal from 'src/components/modal';
import TextInput from 'src/components/input';
// Components
import Navigation from 'src/containers/navigation';
import StockInput from 'src/containers/stockInput';
import StocksDisplayContainer from 'src/containers/stocksDisplayContainer';

interface IStockContainerProps {
    apiKey: string;
    addApiKey: (apiKey: string) => Dispatch<object>;
    replaceEntireList: (updater: IStocks[]) => Dispatch<object>;
    updateStock: (stocksList: IStocks[], index: number, apiKey: string, callback: any) => Dispatch<object>;
}
interface IStockContainerState {
    openModal: boolean;
    inputValue: string;
    inputError: boolean;
}

class StockContainer extends React.Component<IStockContainerProps, IStockContainerState> {
    constructor(props: any) {
        super(props);
        this.state = {
            openModal: true,

            inputValue: '',
            inputError: false
        };
    }
    componentDidMount() {
        let apiKey = localStorage.getItem('stocksApp.quandlApiKey');
        let stocksList = localStorage.getItem('stocksApp.stocksList');
        if (apiKey) {
            this.props.addApiKey(apiKey);
            this.setState({ openModal: false });
        }
        if (stocksList) {
            this.props.replaceEntireList(JSON.parse(stocksList));
        }
    }
    // Modal Methods
    handleOpenModal = () => {
        this.setState({ openModal: true });
    }
    handleCloseModal = () => {
        this.setState({ openModal: false });
    }
    handleInputChange = (e: any) => {
        this.setState({ inputValue: e.currentTarget.value });
    }
    handleSubmitForm = (e: any) => {
        e.preventDefault();
        let apiKey = this.state.inputValue;
        if (apiKey.length < 10) {
            this.setState({ inputError: true });
            return;
        }
        this.setState({ 
            inputError: false,
            openModal: false
        });
        localStorage.setItem('stocksApp.quandlApiKey', apiKey);
    }
    // Stock Actions
    // checkStore = () => {
    //     console.log(store.getState());
    // }
    saveData = () => {
        const currentStore: any = store.getState();
        const stocks = currentStore.stocks.map((stock: any) => {
            return { id: stock.id, isEtf: stock.isEtf };
        });
        localStorage.setItem('stocksApp.stocksList', JSON.stringify(stocks));
    }
    fetchData = () => {
        const currentStore: any = store.getState();
        const { stocks, apiKey } = currentStore;
        this.props.updateStock(stocks, 0, apiKey, this.props.updateStock);
    }
    render() {
        return (
            <div className="stock-container">
                <Navigation onSaveData={this.saveData} onFetchData={this.fetchData} onApiKey={this.handleOpenModal}/>
                {/* <button onClick={this.saveData}>Save Data</button>
                <button onClick={this.fetchData}>Reload Data</button>
                <button onClick={this.handleOpenModal}>API Key</button> */}
                <StockInput />
                <StocksDisplayContainer />
                <Modal
                    isModalOpen={this.state.openModal}
                    handleCloseModal={this.handleCloseModal} 
                >
                    <h2 style={{ textAlign: 'center' }}>Enter Quandl API Key</h2>
                    <TextInput 
                        handleInputChange={this.handleInputChange}
                        inputValue={this.state.inputValue}
                        handleSubmitForm={this.handleSubmitForm} 
                        inputError={this.state.inputError}
                        buttonContent={<i className="fa fa-save" />}
                    />
                </Modal>
            </div>
        );
    }
}

function mapStateToProps(state: any) {
    return {
        apiKey: state.apiKey
    };
}

function mapDispatchToProps(dispatch: Dispatch<Object>) {
    return bindActionCreators(
        {
            addApiKey,
            replaceEntireList,
            updateStock
        },
        dispatch
    );
}

export default connect(mapStateToProps, mapDispatchToProps)(StockContainer);


