import * as React from 'react';
import { findDOMNode } from 'react-dom';
import { Dispatch } from 'redux';
import {
    DragSource,
    DropTarget,
    ConnectDropTarget,
    ConnectDragSource,
    DropTargetMonitor,
    DropTargetConnector,
    DragSourceConnector,
    DragSourceMonitor,
} from 'react-dnd';
import { XYCoord } from 'dnd-core';
const flow = require('lodash/flow');

// Interface
// import { IStocks } from 'src/interface';

// Components
import MobileTableRow from '../components/mobileTableRow';

const rowSource = {
    beginDrag(props: IStocksDisplayRowProps) {
        return {
            id: props.id,
            index: props.index,
        };
    },
};

const rowTarget = {
    hover(props: IStocksDisplayRowProps, monitor: DropTargetMonitor, component: StocksDisplayRow | null) {
        if (!component) {
            return null;
        }
        const dragIndex = monitor.getItem().index;
        const hoverIndex = props.index;

        // Don't replace items with themselves
        if (dragIndex === hoverIndex) {
            return;
        }

        // Determine rectangle on screen
        const hoverBoundingRect = (findDOMNode(
            component,
        ) as Element).getBoundingClientRect();

        // Get vertical middle
        const hoverMiddleY = (hoverBoundingRect.bottom - hoverBoundingRect.top) / 2;

        // Determine mouse position
        const clientOffset = monitor.getClientOffset();

        // Get pixels to the top
        const hoverClientY = (clientOffset as XYCoord).y - hoverBoundingRect.top;

        // Only perform the move when the mouse has crossed half of the items height
        // When dragging downwards, only move when the cursor is below 50%
        // When dragging upwards, only move when the cursor is above 50%
        // Dragging downwards
        if (dragIndex < hoverIndex && hoverClientY < hoverMiddleY) {
            return;
        }

        // Dragging upwards
        if (dragIndex > hoverIndex && hoverClientY > hoverMiddleY) {
            return;
        }

        // Time to actually perform the action
        props.moveRow(dragIndex, hoverIndex);

        // Note: we're mutating the monitor item here!
        // Generally it's better to avoid mutations,
        // but it's good here for the sake of performance
        // to avoid expensive index searches.
        monitor.getItem().index = hoverIndex;
    },
};

export interface IStocksDisplayRowProps {
    id: any;
    // text: string;
    index: number;
    isDragging?: boolean;
    connectDragSource?: ConnectDragSource;
    connectDropTarget?: ConnectDropTarget;
    moveRow: (dragIndex: number, hoverIndex: number) => void;
    data: any;
    toggleStockType: (stocks: any) => Dispatch<object>;
    deleteStock: (stocks: any) => Dispatch<object>;
}

class StocksDisplayRow extends React.Component<IStocksDisplayRowProps> {
    public render() {
        const {
            // text,
            isDragging,
            connectDragSource,
            connectDropTarget,
            data,
            toggleStockType,
            deleteStock
        } = this.props;
        const opacity = isDragging ? 0 : 1;

        return (
            connectDragSource &&
            connectDropTarget &&
            connectDragSource(
                connectDropTarget(
                    // <div 
                    //     style={{ ...style, opacity }}
                    // >
                    //     {data.name}
                    // </div>
                    <div className="mobile-row-draggable">
                        <MobileTableRow 
                            data={data}
                            style={{ opacity }}
                            toggleStockType={toggleStockType}
                            deleteStock={deleteStock}
                        />
                    </div>
                ),
            )
        );
    }
}

export default flow(
    DropTarget('row', rowTarget, (connect: DropTargetConnector) => ({
        connectDropTarget: connect.dropTarget(),
    })),
    DragSource(
        'row',
        rowSource,
        (connect: DragSourceConnector, monitor: DragSourceMonitor) => ({
            connectDragSource: connect.dragSource(),
            isDragging: monitor.isDragging(),
        }),
    )
)(StocksDisplayRow);


