import * as React from 'react';
import { Dispatch } from 'redux';
import './mobileTableRow.css';

// Interface
import { IStocks } from 'src/interface';

interface IMobileTableRowState {
    isRowCollapsed: boolean;
}

interface IMobileTableRowProps {
    // data: object;
    data: IStocks;
    style: object;
    toggleStockType: (stocks: any) => Dispatch<object>;
    deleteStock: (stocks: any) => Dispatch<object>;
}

// export default (props: IMobileTableRowProps) => {
class MobileTableRow extends React.Component<IMobileTableRowProps, IMobileTableRowState> {
    state = {
        isRowCollapsed: true
    };

    onToggleRowCollapse = () => {
        this.setState({ isRowCollapsed: !this.state.isRowCollapsed });
    }
    renderDataCell = (value: number | undefined, title: string, percentage?: number) => {
        const color = percentage ? percentage < 0 ? 'red' : percentage > 0 ? 'green' : 'rgba(0,0,0,0.9)' : 'black';
        const percentageContainer = percentage ? <span style={{ color, fontSize: '0.8em', margin: '2px' }}>{` (${percentage}%) `}</span> : null;

        return (
            <div className="mobile-cell-container">
                <div className="mobile-cell-title">{title}</div>
                <div className="mobile-cell-value"><span className="mobile-float-right">{value}</span>{percentageContainer}</div>
            </div>
        );
    }

    render() {
        const { isRowCollapsed } = this.state;
        const { data, style, toggleStockType, deleteStock } = this.props;
        const { id, isEtf, name, currentBid = 0, currentPrice = 0, high = 0, low = 0, prevCloseDay = 0, prevCloseMonth = 0 } = data;
        const currentBidPercentage = prevCloseDay !== 0 ? Math.floor( (currentBid - prevCloseDay) * 10000 / prevCloseDay) / 100 : 0;
        const currentPricePercentage = prevCloseDay !== 0 ? Math.floor( (currentPrice - prevCloseDay) * 10000 / prevCloseDay) / 100 : 0;
        const prevDayPercentage = prevCloseMonth !== 0 ? Math.floor( (prevCloseDay - prevCloseMonth) * 10000 / prevCloseMonth) / 100 : 0;

        return (
            <div className="mobile-table-row" style={{ ...style }}>
                <div>
                    <div className="mobile-table-cell-main">
                        <a 
                            href={`https://quotes.wsj.com/${isEtf ? 'etf/' : ''}HK/XHKG/${id.substring(1)}/${isEtf ? '' : 'research-ratings'}`}
                            target="_blank"
                            style={{ textDecoration: 'none', textShadow: '0.5px 0.5px black' }}
                        >
                            {`${name || id}`}
                        </a>
                        <div className="mobile-table-cell-main-button-group">
                            <div className="mobile-table-cell-button" onClick={() => { toggleStockType(data); }}>
                                <i className={`fa fa-${isEtf ? 'th' : 'stop'}`} />
                            </div>
                            <div className="mobile-table-cell-button" onClick={() => { deleteStock(data); }}>
                                <i className={`fa fa-trash`} />
                            </div>
                            <div className="mobile-table-cell-button mobile-only">
                                <i className={`fa fa-bars`} onClick={() => { this.onToggleRowCollapse(); }} />
                            </div>
                        </div>
                    </div>
                </div>
                <div className={`mobile-table-cell ${isRowCollapsed ? 'mobile-row-collapsed' : ''}`}>{this.renderDataCell(currentBid, 'current bid', currentBidPercentage)}</div>
                <div className={`mobile-table-cell ${isRowCollapsed ? 'mobile-row-collapsed' : ''}`}>{this.renderDataCell(currentPrice, 'current price', currentPricePercentage)}</div>
                <div className={`mobile-table-cell ${isRowCollapsed ? 'mobile-row-collapsed' : ''}`}>{this.renderDataCell(high, 'day high')}</div>
                <div className={`mobile-table-cell ${isRowCollapsed ? 'mobile-row-collapsed' : ''}`}>{this.renderDataCell(low, 'day low')}</div>
                <div className={`mobile-table-cell ${isRowCollapsed ? 'mobile-row-collapsed' : ''}`}>{this.renderDataCell(prevCloseDay, 'prev close day', prevDayPercentage)}</div>
                <div className={`mobile-table-cell ${isRowCollapsed ? 'mobile-row-collapsed' : ''}`}>{this.renderDataCell(prevCloseMonth, 'prev close month')}</div>
            </div>
        );
    }
}

export default MobileTableRow;


