import * as React from 'react';

export default (props: any) => {
    const { onSaveData, onFetchData, onApiKey } = props;
    return (
        <div className="nav-container">
            <div style={{ width: '40%'}} onClick={onFetchData} className="nav-button"><span className="mobile-hidden">Reload Data</span><span className="mobile-only"><i className="fa fa-download" /></span></div>
            <div style={{ width: '40%'}} onClick={onSaveData} className="nav-button"><span className="mobile-hidden">Save Data</span><span className="mobile-only"><i className="fa fa-save" /></span></div>
            <div style={{ width: '20%'}} onClick={onApiKey} className="nav-button"><span className="mobile-hidden">API Key</span><span className="mobile-only"><i className="fa fa-key" /></span></div>
        </div>
    );
};


